.container{
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-around;
	width: 100%;
	height: 100%;
  }
  .title{
	margin-bottom: 48px;
  }
  .center {
	display: flex;
	flex:1;
	justify-content: center;
	align-items: center;
	position: relative;
  }
  
  @keyframes loader {
	  0%, 10%, 100% {
		  width: 80px;
		  height: 80px;
	  }
	  65% {
		  width: 150px;
		  height: 150px;
	  }
  }
  @keyframes loaderBlock {
	  0%, 30% { 
		  transform: rotate(0);
	  }
	  55% {
		  background-color: #F8B44C;
	  }
	  100% {
		  transform: rotate(90deg);
  
	  }
  }
  @keyframes loaderBlockInverse {
	  0%, 20% { 
		  transform: rotate(0);
	  }
	  55% {
		  background-color: #F8B44C;
	  }
	  100% {
		  transform: rotate(-90deg);
	  }
  }
  .loader {
	  position: absolute;
	  top: 200px;
	  left: 50%;
	  width: 80px;
	  height: 80px;
	  transform: translate(-50%, -50%) rotate(45deg) translate3d(0,0,0);
	  animation: loader 1s infinite ease-in-out;
  
	  
  }
  .loader span {
	position: absolute;
	display: block;
	width: 40px;
	height: 40px;
	background-color: #FF0505;
	animation: loaderBlock 1s infinite ease-in-out both;
  
   
  }
  .loader span:nth-child(1) {
	top: 0;
	left: 0;
  }
  .loader span:nth-child(2) {
	top: 0;
	right: 0;
	animation: loaderBlockInverse 1s infinite ease-in-out both;
  }
  .loader span:nth-child(3) {
	bottom: 0;
	left: 0;
	animation: loaderBlockInverse 1s infinite ease-in-out both;
  }
  .loader span:nth-child(4) {
	bottom: 0;
	right: 0;
  }